import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { Spin, Steps, Icon, Tooltip, Tag } from 'antd';
import moment from 'moment';

import DocumentTitle from '../../component/document-title';
import ViewSourceCode from '../../component/view-source-code';
import github from '../../lib/github';
import * as todoAction from '../../redux/todo';
import CONFIG from '../../config.json';
import { diffTime } from '../../lib/utils';

class Todo extends Component {
  state = {};

  async componentWillMount() {
    let { number } = this.props.match.params;
    if (number) {
      await this.getTodo(number);
    }
  }

  async componentWillReceiveProps(nextProp) {
    const { number } = nextProp.match.params;
    if (number && number !== this.props.match.params.number) {
      await this.getTodo(nextProp.match.params.number);
    }
  }

  async getTodo(number) {
    let todo = {};
    try {
      const {
        data
      } = await github.get(
        `/repos/${CONFIG.owner}/${CONFIG.todo_repo}/issues/${number}`,
        {
          headers: {
            Accept: 'application/vnd.github.v3.html'
          },
          responseType: 'text'
        }
      );
      todo = data;
    } catch (err) {
      console.error(err);
    }
    this.props.setTodo({ [number]: todo });
    return todo;
  }

  render() {
    const { number } = this.props.match.params;
    const todo = this.props.TODO[number] || {};
    return (
      <DocumentTitle title={todo.title} suffix={['TODO']}>
        <Spin spinning={!Object.keys(todo).length}>
          <div className="toolbar-container">
            <div className="edit-this-page">
              <Tooltip placement="topLeft" title="" arrowPointAtCenter>
                <ViewSourceCode file="pages/todo/index.js">
                  <a href="javascript: void 0" target="_blank">
                    <Icon
                      type="code"
                      style={{
                        fontSize: '3rem'
                      }}
                    />
                  </a>
                </ViewSourceCode>
              </Tooltip>
            </div>
            {todo.title
              ? <h2 style={{ textAlign: 'center', margin: '1rem 0' }}>
                  {todo.title}
                  <Tooltip placement="topLeft" title="">
                    <a
                      href={`https://github.com/${CONFIG.owner}/${CONFIG.todo_repo}/issues/${todo.number}`}
                      target="_blank"
                    >
                      <Icon type="edit" />
                    </a>
                  </Tooltip>
                </h2>
              : ''}
            <Steps
              style={{
                margin: '2rem 0'
              }}
            >
              <Steps.Step
                status="finish"
                title=""
                description={`${moment(new Date(todo.created_at)).format('YYYY-MM-DD HH:mm:ss')}`}
                icon={<Icon type="book" />}
              />
              <Steps.Step
                status={todo.closed_at ? 'finish' : 'wait'}
                title=""
                description={
                  todo.closed_at
                    ? (() => {
                        const diff = diffTime(new Date(todo.created_at))(
                          new Date(todo.closed_at)
                        );
                        return `${diff.days ? diff.days + '' : ''} ${diff.hours || diff.days ? diff.hours + '' : ''}${diff.minutes || diff.hours ? diff.minutes + '' : ''}${diff.seconds}`;
                      })()
                    : '...'
                }
                icon={<Icon type="clock-circle-o" />}
              />
              <Steps.Step
                status={todo.closed_at ? 'finish' : 'wait'}
                title=""
                description={
                  todo.closed_at
                    ? `${moment(new Date(todo.closed_at)).format('YYYY-MM-DD HH:mm:ss')}`
                    : ''
                }
                icon={<Icon type="check" />}
              />
            </Steps>
            <div style={{ margin: '2rem 0' }}>
              {(todo.labels || []).map(label => {
                return (
                  <Tag key={label.id} color={'#' + label.color}>
                    {label.name}
                  </Tag>
                );
              })}
            </div>
            <div
              className="markdown-body"
              style={{
                fontSize: '1.6rem',
                minHeight: '20rem'
              }}
              dangerouslySetInnerHTML={{
                __html: todo.body_html
              }}
            />
          </div>
        </Spin>
      </DocumentTitle>
    );
  }
}
export default connect(
  function mapStateToProps(state) {
    return { TODO: state.TODO };
  },
  function mapDispatchToProps(dispatch) {
    return bindActionCreators(
      {
        setTodo: todoAction.set
      },
      dispatch
    );
  }
)(withRouter(Todo));



